/* PIN-PAD */
@font-face {
    font-family: "DS-Digital-Bold";
    src: url("./resources/fonts/DS-DIGIB.otf") format("opentype"), /* Open Type Font */ url("./resources/fonts/DS-DIGIB.woff") format("woff"); /* Modern Browsers */
    font-weight: normal;
    font-style: normal;
}

.app {
    width: 100%;
    height: 100vh;
    position: relative;
    min-height: 560px;
    overflow: hidden;
}
.app__background {
    position: absolute;
    z-index: -1;
    width: 100%;
    height: 100%;
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
}

.mod--bg_pin-pad {
    background: url("./resources/bg.jpg");
    filter: blur(5px);
}

.pin-pad {
    transition: all 0.5s ease-in-out;
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 20vw;
    height: 30vw;
    max-width: 360px;
    max-height: 560px;
    min-width: 240px;
    min-height: 375px;
    font-family: Arial, Helvetica, sans-serif;
    background: linear-gradient(to right, #2f3744 0%, #272e38 100%);
    box-shadow: inset 0px 0px 2px 0px #59616e, 0px 7px 0px 0px #0d141a, 0px 10px 15px 5px #000000;
    z-index: 2;
    animation: showUp 1s ease-out forwards;
    &:after {
        content: "";
        width: 15px;
        height: 100%;
        position: absolute;
        right: -15px;
        border-top-right-radius: 15px;
        border-bottom-right-radius: 15px;
        background: #1b1f28;
        box-shadow: 0px 7px 0px 0px #0d141a;
    }
    &:before {
        content: "";
        width: 15px;
        height: 100%;
        position: absolute;
        left: -15px;
        border-top-left-radius: 15px;
        border-bottom-left-radius: 15px;
        background: #5c6471;
        box-shadow: 0px 7px 0px 0px #0d141a;
    }
    &__screen {
        width: 90%;
        height: calc(25% - 20px);
        background: #dbe4eb;
        border-radius: 5px;
        box-shadow: inset 0px 5px 4px 2px #a6b3bc;
        margin-top: 20px;
        font-size: 4rem;
        display: flex;
        justify-content: center;
        align-items: center;
        &_txt {
            font-family: "DS-Digital-Bold";
            color: #4b4a4c;
            line-height: 4rem;
        }
    }
    &__keyboard {
        width: calc(100% - 10px);
        height: calc(70% - 10px);
        padding: 5px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        &_btn {
            display: flex;
            width: calc(33.3% - 12px);
            height: calc(25% - 12px);
            border-radius: 8px;
            background: linear-gradient(to bottom, #364556 0%, #334050 25%, #172630 100%);
            margin: 4px;
            border: 2px solid #0d141a;
            cursor: pointer;
            box-shadow: inset 1px 0px 1px 0px #4c535e, 1px 0px 2px 0px #434b4e;
            color: white;
            &:active {
                background: linear-gradient(to top, #364556 0%, #334050 25%, #172630 100%);
                box-shadow: 1px 0px 2px 0px #434b4e;
                color: rgb(207, 207, 207);
            }
            &_txt {
                display: block;
                margin: auto;
                font-weight: 600;
                font-size: 2rem;
                -webkit-text-stroke: thin #a2aec0;
            }
            &.mod--red {
                background: linear-gradient(to bottom, #ae5260 0%, #af4e5f 25%, #913c48 100%);
                box-shadow: inset 1px 0px 1px 0px #c17081, 1px 0px 2px 0px #434b4e;
                &:active {
                    background: linear-gradient(to top, #ae5260 0%, #af4e5f 25%, #913c48 100%);
                    box-shadow: 1px 0px 2px 0px #434b4e;
                }
            }
            &.mod--green {
                background: linear-gradient(to bottom, #0caea3 0%, #009090 25%, #008580 100%);
                box-shadow: inset 1px 0px 1px 0px #17d6c9, 1px 0px 2px 0px #434b4e;
                &:active {
                    background: linear-gradient(to top, #0caea3 0%, #009090 25%, #008580 100%);
                    box-shadow: 1px 0px 2px 0px #434b4e;
                }
            }
        }
    }
}

@keyframes showUp {
    from {
        top: 250%;
    }
    to {
        top: 50%;
    }
}
